"use client";

import { useState } from "react";
import { Document, Page, pdfjs } from "react-pdf";
import ParticleBackground from "../../components/particle-background";
import Navbar from "../../components/navbar";
import Chatbox from "../../components/chatbox";
import styles from "./style.module.css";

pdfjs.GlobalWorkerOptions.workerSrc = `https://cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.min.js`;

const parseBoldText = (text: string): string => {
  return text.replace(/\*\*(.*?)\*\*/g, "<strong>$1</strong>");
};

export default function PdfSummarizer() {
  const [file, setFile] = useState<File | null>(null);
  const [pdfURL, setPdfURL] = useState<string | null>(null);
  const [isDragging, setIsDragging] = useState(false);
  const [summary, setSummary] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);
  const [numPages, setNumPages] = useState<number | null>(null);

  // Handle file selection
  const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const selectedFile = event.target.files?.[0];
    if (selectedFile && selectedFile.type === "application/pdf") {
      setFile(selectedFile);
      setPdfURL(URL.createObjectURL(selectedFile));
    } else {
      alert("Please upload a valid PDF file.");
    }
  };

  // Handle file drop
  const handleDrop = (event: React.DragEvent<HTMLDivElement>) => {
    event.preventDefault();
    setIsDragging(false);

    const droppedFile = event.dataTransfer.files?.[0];
    if (droppedFile && droppedFile.type === "application/pdf") {
      setFile(droppedFile);
      setPdfURL(URL.createObjectURL(droppedFile));
    } else {
      alert("Please upload a valid PDF file.");
    }
  };

  // Handle summarization
  const handleSummarize = async () => {
    if (!file) {
      alert("Please upload a PDF file first.");
      return;
    }

    setLoading(true);
    const formData = new FormData();
    formData.append("file", file);

    try {
      const response = await fetch("http://localhost:5000/summarize-pdf", {
        method: "POST",
        body: formData,
      });

      if (!response.ok) {
        throw new Error("Failed to summarize the PDF.");
      }

      const data = await response.json();
      setSummary(data.summary);
    } catch (error) {
      console.error(error);
      alert("An error occurred while summarizing the PDF.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <>
      <main className="relative min-h-screen">
        {/* Fixed Navbar at the top */}
        <Navbar />

        {/* Particle Background */}
        <ParticleBackground />

        <div className="relative z-10 pt-24 flex flex-col items-center justify-center text-white text-center p-6">
          <h1 className="text-5xl font-bold mb-4">PDF Summarizer</h1>
          <p className="text-lg text-gray-300 mb-8 max-w-xl">
            Upload a PDF file below to generate a concise summary of its content.
          </p>

          {/* Drag-and-drop file upload area */}
          <div
            className={`w-full max-w-4xl p-8 border-2 border-dashed rounded-lg ${
              isDragging ? "border-blue-500 bg-blue-500/10" : "border-gray-300 bg-gray-800/10"
            } transition-all`}
            onDrop={handleDrop}
            onDragOver={(e) => {
              e.preventDefault();
              setIsDragging(true);
            }}
            onDragLeave={() => setIsDragging(false)}
          >
            <input
              type="file"
              id="file-upload"
              className="hidden"
              accept="application/pdf"
              onChange={handleFileChange}
            />
            <label htmlFor="file-upload" className="cursor-pointer flex flex-col items-center justify-center space-y-2">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-12 w-12 text-gray-400"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M7 16a4 4 0 01-.88-7.903A5 5 0 1115.9 6L16 6a5 5 0 011 9.9M15 13l-3-3m0 0l-3 3m3-3v12" />
              </svg>
              <p className="text-gray-400">{file ? `Selected file: ${file.name}` : "Drag & drop a PDF file here or click to upload"}</p>
            </label>
          </div>

          {/* Display the selected file name */}
          {file && (
            <div className="mt-4 text-gray-300">
              <p>
                Selected file: <span className="font-semibold">{file.name}</span>
              </p>
              <button
                onClick={handleSummarize}
                disabled={loading}
                className="mt-4 px-6 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:bg-blue-400"
              >
                {loading ? <div className={styles.loader}></div> : "Summarize PDF"}
              </button>
            </div>
          )}

          {/* Display the summary */}
          {summary && (
            <div className="mt-8 w-full max-w-[90%] flex flex-col md:flex-row gap-6">
              {/* PDF Preview (Left Side) */}
              <div className="w-full md:w-1/2">
                {pdfURL ? (
                  <div className="relative bg-gray-800 rounded-lg overflow-hidden">
                    <Document
                      file={pdfURL}
                      onLoadSuccess={({ numPages }) => setNumPages(numPages)}
                      className="border border-gray-600 rounded-lg"
                    >
                      {Array.from(new Array(numPages), (el, index) => (
                        <Page key={`page_${index + 1}`} pageNumber={index + 1} width={300} />
                      ))}
                    </Document>
                  </div>
                ) : (
                  <div className="relative aspect-video bg-gray-800 rounded-lg flex items-center justify-center">
                    <p className="text-gray-400">PDF Preview Placeholder</p>
                  </div>
                )}
              </div>

              {/* Summary (Right Side) */}
              <div className="w-full md:w-1/2 p-6 bg-white rounded-lg text-left opacity-0 animate-fade-in-up">
                <h2 className="text-2xl font-bold mb-4 text-black">Summary</h2>
                <div className="max-h-96 overflow-y-auto">
                  <p
                    className="text-black whitespace-pre-line opacity-0 animate-fade-in-delay"
                    dangerouslySetInnerHTML={{ __html: parseBoldText(summary) }}
                  />
                </div>
              </div>
            </div>
          )}

          {/* Render Chatbox only when summary is available */}
          {summary && <Chatbox summary={summary} />}
        </div>
      </main>
    </>
  );
}
